.nameList {
    list-style: none;
}

.inputArea{
    flex-direction: column;
    align-items: center;
    display: flex;
    justify-content: center;   
}

.input{
    border: 4px solid #ee0d0d;
    border-radius: 5px;
    width: 80%;
    padding: 15px 22px;
    margin: 10px 5px;
    font-size: large;
    text-align: center;
}

.inputButton{
    color: #fff !important;
    text-transform: uppercase;
    text-decoration: none;
    background: #ed3330;
    padding: 20px;
    border-radius: 5px;
    display: inline-block;
    border: none;
    transition: all 0.4s ease 0s;
}

.inputButton:hover {
    background: #434343;
    letter-spacing: 1px;
    -webkit-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
    -moz-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
    box-shadow: 5px 40px -10px rgba(0,0,0,0.57);
    transition: all 0.4s ease 0s;
}
